/**
 * Message Formatter Utility for Admin Dashboard
 * 
 * Provides consistent message formatting, tone, and copy guidelines
 * for all user-facing notifications across the admin interface.
 * 
 * Guidelines:
 * - Use sentence case for readability
 * - Be clear, concise, and action-oriented
 * - Avoid technical jargon in user messages
 * - Maintain consistent tone and style
 * - Include relevant context when helpful
 */

// Entity name mappings for consistent terminology
export const ENTITY_NAMES = {
  PROMOTER: 'Promoter',
  CUSTOMER: 'Customer', 
  ADMIN: 'Admin',
  PIN: 'Pin',
  WITHDRAWAL: 'Withdrawal',
  PAYMENT: 'Payment',
  USER: 'User',
  ACCOUNT: 'Account',
  PROFILE: 'Profile',
  RECORD: 'Record',
  DATA: 'Data'
};

// Action types for consistent messaging
export const ACTIONS = {
  CREATE: 'create',
  UPDATE: 'update', 
  DELETE: 'delete',
  ACTIVATE: 'activate',
  DEACTIVATE: 'deactivate',
  APPROVE: 'approve',
  REJECT: 'reject',
  ALLOCATE: 'allocate',
  ASSIGN: 'assign',
  REMOVE: 'remove',
  EXPORT: 'export',
  IMPORT: 'import',
  REFRESH: 'refresh',
  LOAD: 'load',
  SAVE: 'save',
  RESET: 'reset'
};

// Status mappings for consistent display
export const STATUS_DISPLAY = {
  active: 'Active',
  inactive: 'Inactive',
  pending: 'Pending',
  approved: 'Approved',
  rejected: 'Rejected',
  completed: 'Completed',
  processing: 'Processing',
  cancelled: 'Cancelled',
  expired: 'Expired'
};

/**
 * Format success messages with consistent structure
 */
export const formatSuccessMessage = (action, entity, details = '') => {
  const entityName = ENTITY_NAMES[entity?.toUpperCase()] || entity;
  
  switch (action) {
    case ACTIONS.CREATE:
      return details 
        ? `${entityName} created successfully. ${details}`
        : `${entityName} created successfully`;
        
    case ACTIONS.UPDATE:
      return details
        ? `${entityName} updated successfully. ${details}`
        : `${entityName} updated successfully`;
        
    case ACTIONS.DELETE:
      return `${entityName} deleted successfully`;
      
    case ACTIONS.ACTIVATE:
      return `${entityName} activated successfully`;
      
    case ACTIONS.DEACTIVATE:
      return `${entityName} deactivated successfully`;
      
    case ACTIONS.APPROVE:
      return `${entityName} approved successfully`;
      
    case ACTIONS.REJECT:
      return `${entityName} rejected successfully`;
      
    case ACTIONS.ALLOCATE:
      return details
        ? `${details} allocated to ${entityName} successfully`
        : `${entityName} allocation completed successfully`;
        
    case ACTIONS.ASSIGN:
      return `${entityName} assigned successfully`;
      
    case ACTIONS.REMOVE:
      return `${entityName} removed successfully`;
      
    case ACTIONS.EXPORT:
      return `${entityName} data exported successfully`;
      
    case ACTIONS.IMPORT:
      return `${entityName} data imported successfully`;
      
    case ACTIONS.REFRESH:
      return details
        ? `${entityName} data refreshed. ${details}`
        : `${entityName} data refreshed successfully`;
        
    case ACTIONS.LOAD:
      return `${entityName} data loaded successfully`;
      
    case ACTIONS.SAVE:
      return `${entityName} changes saved successfully`;
      
    case ACTIONS.RESET:
      return `${entityName} reset to default values`;
      
    default:
      return `${entityName} ${action} completed successfully`;
  }
};

/**
 * Format error messages with helpful context
 */
export const formatErrorMessage = (action, entity, error = '', suggestions = '') => {
  const entityName = ENTITY_NAMES[entity?.toUpperCase()] || entity;
  const baseMessage = `Failed to ${action} ${entityName.toLowerCase()}`;
  
  let message = baseMessage;
  
  if (error) {
    // Clean up technical error messages for users
    const cleanError = cleanTechnicalError(error);
    message += `. ${cleanError}`;
  }
  
  if (suggestions) {
    message += ` ${suggestions}`;
  } else {
    message += '. Please try again';
  }
  
  return message;
};

/**
 * Format validation error messages
 */
export const formatValidationMessage = (field, rule, value = '') => {
  const fieldName = field.charAt(0).toUpperCase() + field.slice(1).replace(/([A-Z])/g, ' $1');
  
  switch (rule) {
    case 'required':
      return `${fieldName} is required`;
      
    case 'email':
      return `Please enter a valid email address`;
      
    case 'phone':
      return `Please enter a valid phone number`;
      
    case 'minLength':
      return `${fieldName} must be at least ${value} characters long`;
      
    case 'maxLength':
      return `${fieldName} must not exceed ${value} characters`;
      
    case 'pattern':
      return `${fieldName} format is invalid`;
      
    case 'unique':
      return `${fieldName} already exists. Please use a different value`;
      
    case 'numeric':
      return `${fieldName} must be a valid number`;
      
    case 'positive':
      return `${fieldName} must be greater than zero`;
      
    case 'range':
      return `${fieldName} must be between ${value}`;
      
    default:
      return `${fieldName} is invalid`;
  }
};

/**
 * Format status change messages
 */
export const formatStatusMessage = (entity, oldStatus, newStatus, name = '') => {
  const entityName = ENTITY_NAMES[entity?.toUpperCase()] || entity;
  const displayOldStatus = STATUS_DISPLAY[oldStatus] || oldStatus;
  const displayNewStatus = STATUS_DISPLAY[newStatus] || newStatus;
  
  const entityRef = name ? `${entityName} "${name}"` : entityName;
  
  return `${entityRef} status updated from ${displayOldStatus} to ${displayNewStatus}`;
};

/**
 * Format quantity/count messages
 */
export const formatQuantityMessage = (action, quantity, entity, details = '') => {
  const entityName = ENTITY_NAMES[entity?.toUpperCase()] || entity;
  const pluralEntity = quantity === 1 ? entityName : `${entityName}s`;
  
  switch (action) {
    case ACTIONS.CREATE:
      return `${quantity} ${pluralEntity} created successfully`;
      
    case ACTIONS.UPDATE:
      return `${quantity} ${pluralEntity} updated successfully`;
      
    case ACTIONS.DELETE:
      return `${quantity} ${pluralEntity} deleted successfully`;
      
    case ACTIONS.EXPORT:
      return `${quantity} ${pluralEntity} exported successfully`;
      
    case ACTIONS.IMPORT:
      return `${quantity} ${pluralEntity} imported successfully`;
      
    case ACTIONS.LOAD:
      return details
        ? `Loaded ${quantity} ${pluralEntity}. ${details}`
        : `Loaded ${quantity} ${pluralEntity}`;
        
    case ACTIONS.ALLOCATE:
      return `${quantity} ${pluralEntity} allocated successfully`;
      
    default:
      return `${quantity} ${pluralEntity} processed successfully`;
  }
};

/**
 * Format time-based messages
 */
export const formatTimeMessage = (action, duration, entity = '') => {
  const entityName = entity ? ` ${ENTITY_NAMES[entity?.toUpperCase()] || entity}` : '';
  
  if (duration < 1000) {
    return `${entityName} ${action} completed in ${duration}ms`;
  } else if (duration < 60000) {
    const seconds = (duration / 1000).toFixed(1);
    return `${entityName} ${action} completed in ${seconds}s`;
  } else {
    const minutes = Math.floor(duration / 60000);
    const seconds = Math.floor((duration % 60000) / 1000);
    return `${entityName} ${action} completed in ${minutes}m ${seconds}s`;
  }
};

/**
 * Clean technical error messages for user consumption
 */
export const cleanTechnicalError = (error) => {
  if (typeof error !== 'string') {
    error = error?.message || error?.toString() || 'Unknown error';
  }
  
  // Remove technical prefixes and codes
  error = error
    .replace(/^Error:\s*/i, '')
    .replace(/^[A-Z0-9_]+:\s*/i, '')
    .replace(/\s*\(code:\s*\d+\)$/i, '')
    .replace(/\s*at\s+.*$/i, '')
    .replace(/\s*in\s+.*\.js.*$/i, '');
  
  // Replace technical terms with user-friendly ones
  const replacements = {
    'ECONNREFUSED': 'Connection failed',
    'ETIMEDOUT': 'Request timed out',
    'ENOTFOUND': 'Service not found',
    'ECONNRESET': 'Connection lost',
    'fetch failed': 'Network request failed',
    'NetworkError': 'Network error',
    'TypeError': 'Invalid data',
    'ReferenceError': 'System error',
    'SyntaxError': 'Data format error',
    'null is not an object': 'Missing data',
    'undefined is not a function': 'System error',
    'Cannot read property': 'Missing information',
    'Cannot access before initialization': 'System initialization error'
  };
  
  for (const [technical, friendly] of Object.entries(replacements)) {
    if (error.includes(technical)) {
      error = friendly;
      break;
    }
  }
  
  // Ensure first letter is capitalized and ends with period if not already
  error = error.charAt(0).toUpperCase() + error.slice(1);
  if (!error.endsWith('.') && !error.endsWith('!') && !error.endsWith('?')) {
    error += '.';
  }
  
  return error;
};

/**
 * Format confirmation messages for destructive actions
 */
export const formatConfirmationMessage = (action, entity, name = '', consequences = '') => {
  const entityName = ENTITY_NAMES[entity?.toUpperCase()] || entity;
  const entityRef = name ? `${entityName} "${name}"` : `this ${entityName.toLowerCase()}`;
  
  let message = `Are you sure you want to ${action} ${entityRef}?`;
  
  if (consequences) {
    message += ` ${consequences}`;
  } else if (action === ACTIONS.DELETE) {
    message += ' This action cannot be undone.';
  }
  
  return message;
};

/**
 * Format loading messages
 */
export const formatLoadingMessage = (action, entity = '') => {
  const entityName = entity ? ` ${ENTITY_NAMES[entity?.toUpperCase()] || entity}` : '';
  
  switch (action) {
    case ACTIONS.CREATE:
      return `Creating${entityName}...`;
    case ACTIONS.UPDATE:
      return `Updating${entityName}...`;
    case ACTIONS.DELETE:
      return `Deleting${entityName}...`;
    case ACTIONS.LOAD:
      return `Loading${entityName} data...`;
    case ACTIONS.SAVE:
      return `Saving${entityName} changes...`;
    case ACTIONS.EXPORT:
      return `Exporting${entityName} data...`;
    case ACTIONS.IMPORT:
      return `Importing${entityName} data...`;
    default:
      return `Processing${entityName}...`;
  }
};

/**
 * Format permission/access messages
 */
export const formatAccessMessage = (action, entity, reason = '') => {
  const entityName = ENTITY_NAMES[entity?.toUpperCase()] || entity;
  
  let message = `You don't have permission to ${action} ${entityName.toLowerCase()}`;
  
  if (reason) {
    message += `. ${reason}`;
  } else {
    message += '. Please contact your administrator for access.';
  }
  
  return message;
};

// Export all formatters as a single object for easy import
export const MessageFormatter = {
  success: formatSuccessMessage,
  error: formatErrorMessage,
  validation: formatValidationMessage,
  status: formatStatusMessage,
  quantity: formatQuantityMessage,
  time: formatTimeMessage,
  confirmation: formatConfirmationMessage,
  loading: formatLoadingMessage,
  access: formatAccessMessage,
  cleanError: cleanTechnicalError
};

export default MessageFormatter;
